import React from "react";
import Typed from 'react-typed';
import "../css/Form.css";
const login_link = "";
const signin_link = "";
const post_link = "";
export default function Form(props) {
  let send, btntext;
  if (props.page === "login") {
    send = login_link;
    btntext = "Login";
  } else if (props.page === "post") {
    send = post_link;
    btntext = "Send";
  } else if (props.page === "signin") {
    send = signin_link;
    btntext = "Signup";
  }

  return (
      <div className="container my-2 form-container ">
        <form action={send} method="post">
          {props.page === "post" || //either login or signup page
            <>        
              <input
                className="form-control my-3"
                placeholder="Please Enter your name"
                type="text"
                name="name"
              /><input
                className="form-control my-3"
                placeholder="Please Enter your password"
                type="password"
                name="password"
              />
              </>
          }{props.page === "signin"&& <input
                className="form-control my-3"
                placeholder="Please enter your email"
                type="email"
                name="email"
              />}
             
          
          {props.page !== "post" || (
            <div className="form-floating">
              <h3 id="h3" className="my-3">
                <Typed
                  strings={[
                    "Please Enter your query",
                    " Dr Alok will reply shortly",
                  ]}
                  backSpeed={15}
                  smartBackspace={true}
                  backDelay={1200}
                  startDelay={1000}
                  typeSpeed={25}
                  loop={false}
                />
              </h3>
              <hr className="dropdown-divider" />
              <textarea
                className="form-control my-4"
                placeholder="Enter question here"
                style={{ height: 100 }}
              ></textarea>
            </div>
          )}
          <div className="container">
            <input
              className="btn btn-success btn-outline-light my-2"
              type="submit"
              value={btntext}
              id="submit"/>
          </div>
        </form>
      </div>
  )};
